
.passwrap {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    overflow: auto;
}

.passsupport {
    -ms-flex: 4 1;
        flex: 4 1;
}

.wrappass {
    background: #fcfc9a;
    -ms-flex: 4 1;
        flex: 4 1;
    margin-top: 1%;
    margin-left: 1%;
    margin-right: 1%;
}

.faild {
    color: red;
    text-align: center;
}

.start {
    min-height: 80px;
    border-bottom: 1px solid #9e9e9e;
    text-align: center;
    padding: 5px;
}
.wrapbtn {
    display: -ms-flexbox;
    display: flex;
}

.enter {
    -ms-flex: 6 1;
        flex: 6 1;
    text-align: center;
}


@media (max-width: 876px) {
    .passsupport {
     display: none;
    }
   }


